      .line-numbers {
        width: 35px;
        text-align: left;
        margin-top: 0px;
        background-color: darkseagreen;
        padding-top: 13px;
      }

      .line-numbers span {
        counter-increment:  linenumber;
      }

      .line-numbers span::before {
        content: counter(linenumber);
        display: block;
        color: #506882;
